Family.push({ id:"28", title: "Nguyn nh Liu", father: "0", lunar: ("YEAR-11-07")} );

let events : Array<CalendarEvent> = [];

Family.map((human)=>{
    const row: CalendarEvent = human as CalendarEvent;
    const today = new Date();
    const year = today.getFullYear().toString();
    let month = (today.getMonth() + 1).toString();

    if( !human.lunar ){
        return false;
    }
    
    row.lunar = human.lunar.replace("YEAR", year).replace("MONTH", month);
    const date = moment(row.lunar);
    row.date = lunar(parseInt(date.format('D')), parseInt(date.format('M')));
    row.eventName = `D c [${human.title}] (${human.lunar})`;
    row.deathDate = true;
    events.push(row);
    return true;
});

export {events};
