import moment from 'moment';
import lunar from '../../Libraries/Lunnar/lunar';

export const Holidays = [
    { solar:"YEAR-01-01", title: "Tt Dng Lch" },
    { solar:"2022-01-02", offset:true },
    { solar:"2022-01-03", offset:true },

    { solar:"2022-01-29", title: "Tt Nguyn n" },
    { solar:"2022-01-30", title: "Tt Nguyn n" },
    { solar:"2022-01-31", title: "Tt Nguyn n" },
    { solar:"2022-02-01", title: "Tt Nguyn n" },
    { solar:"2022-02-02", title: "Tt Nguyn n" },
    { solar:"2022-02-03", title: "Tt Nguyn n" },
    { solar:"2022-02-04", title: "Tt Nguyn n" },
    { solar:"2022-02-05", title: "Tt Nguyn n" },
    { solar:"2022-02-06", title: "Tt Nguyn n" },

    { lunar:"YEAR-03-10", title: "Gi T Hng Vng" },
    { lunar:"YEAR-03-11", title: "Ngh Gi T Hng Vng" },

    { solar:"YEAR-04-30", title: "Ngy Thng nht t nc" },
    { solar:"YEAR-05-01", title: "Ngy Quc t Lao ng" },
    { solar:"2022-05-02", title: "Ngh Ngy Thng nht" },
    { solar:"2022-05-03", title: "Ngh Ngy Quc t Lao ng" },

    { solar:"2022-09-01", title: "Ngh Ngy Quc khnh" },
    { solar:"YEAR-09-02", title: "Ngy Quc khnh" },
];

let events : Array<CalendarEvent> = [];


Holidays.map((event)=>{
    //const row: { title: string; lunar?: any; solar?: any, date?:any, type?: string } = { ...event };
    let row : CalendarEvent = event as CalendarEvent;
    row.type = "holiday";

    const today = new Date();
    const year = today.getFullYear().toString();
    let month = (today.getMonth() + 1).toString();

    if( row.solar ){
        row.solar = row.solar.replace("YEAR", year).replace("MONTH", month);
        row.date = moment(row.solar);
        events.push(row); return true;
    }

    if( row.lunar ){
        row.lunar = row.lunar.replace("YEAR", year).replace("MONTH", month);
        const date = moment(row.lunar);
        row.date = lunar(parseInt(date.format('D')), parseInt(date.format('M')));
        events.push(row); return true;
    }
    return true;
});

export {events};
